export function getHideHeaderMixin() {
  return {
    data: function data() {
      var horizontal = false;
      // horizontal = document.documentElement.clientHeight < 420;
      return {
        showHeader: !horizontal
      };
    },
    mounted: function mounted() {
      if (!this.showHeader) {
        this.setHeaderStyle('none');
      }
    },
    methods: {
      onToggleShowHeader: function onToggleShowHeader() {
        this.showHeader = !this.showHeader;
        var style = 'none';
        if (this.showHeader) {
          style = 'block';
        }
        this.setHeaderStyle(style);
      },
      setHeaderStyle: function setHeaderStyle(style) {
        var header = document.querySelector('uni-page-head') || document.querySelector('.uni-page-head-wrap');
        if (header) {
          // @ts-ignore
          header.style.display = style;
        }
      }
    }
  };
}